import { getAllPosts } from '@/lib/posts';
import Link from 'next/link';
import { format } from 'date-fns';
import { ko } from 'date-fns/locale';

export default function Home() {
  const posts = getAllPosts();
  const recentPosts = posts.slice(0, 5); // 최근 5개만

  return (
    <div className="container-minimal">
      {/* Intro Section - Minimal & Strong */}
      <section className="mb-20">
        <h1 className="text-3xl md:text-4xl font-extrabold mb-6 leading-tight tracking-tight">
          생명과 데이터를 잇는<br />
          <span className="text-[var(--primary)]">연구개발자</span>입니다.
        </h1>
        <p className="text-lg md:text-xl text-[var(--foreground-secondary)] leading-relaxed font-serif">
          심장 자기 신호 처리와 텍스트 마이닝을 통해 데이터에서 의미를 찾습니다.<br />
          "데이터 안에 답이 있다면, 저는 그걸 꺼내오는 사람입니다."
        </p>

        <div className="mt-8 flex gap-4 font-sans text-sm font-medium">
          <Link href="/about" className="border-b-2 border-[var(--primary)] text-[var(--foreground)] hover:text-[var(--primary)] pb-0.5 transition-colors">
            More about me
          </Link>
          <a href="https://github.com/happy-haru" target="_blank" className="border-b-2 border-transparent hover:border-[var(--primary)] text-[var(--foreground-secondary)] hover:text-[var(--primary)] pb-0.5 transition-all">
            GitHub Profile
          </a>
        </div>
      </section>

      {/* Latest Posts - Scriptor Style List */}
      <section>
        <div className="flex items-center justify-between mb-10 border-b border-[var(--border)] pb-4">
          <h2 className="text-xl font-bold font-sans">Latest Posts</h2>
          <Link href="/blog" className="text-sm text-[var(--primary)] hover:text-[var(--primary-hover)] font-sans font-semibold">
            View all posts &rarr;
          </Link>
        </div>

        <div className="space-y-12">
          {recentPosts.map((post) => (
            <article key={post.slug} className="group">
              <div className="flex items-baseline justify-between mb-2">
                <Link href={`/blog/${post.slug}`}>
                  <h3 className="text-2xl font-bold group-hover:text-[var(--primary)] transition-colors font-sans leading-snug">
                    {post.title}
                  </h3>
                </Link>
              </div>

              <div className="mb-3 text-sm text-[var(--foreground-secondary)] font-sans flex gap-3">
                <time dateTime={post.date}>
                  {format(new Date(post.date), 'MMM d, yyyy', { locale: ko })}
                </time>
                <span>&middot;</span>
                <span className="text-[var(--primary)] font-medium uppercase tracking-wide text-xs self-center">
                  {post.category[0]}
                </span>
              </div>

              <p className="text-[var(--foreground-secondary)] mb-4 leading-relaxed line-clamp-2 md:line-clamp-none">
                {post.description}
              </p>

              <Link href={`/blog/${post.slug}`} className="inline-block text-sm font-bold text-[var(--foreground)] hover:text-[var(--primary)] transition-colors border-b border-[var(--border)] pb-0.5 hover:border-[var(--primary)] font-sans">
                Read more
              </Link>
            </article>
          ))}

          {recentPosts.length === 0 && (
            <p className="text-[var(--foreground-secondary)] italic">작성된 포스트가 없습니다.</p>
          )}
        </div>
      </section>
    </div>
  );
}
